  position: relative;
}
.breadcrumbs ul li a {
  color: #fff;
}
.breadcrumbs ul li:not(:last-of-type)::after, .breadcrumbs ul li:first-of-type::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -1.5812591508rem;
  transform: translate(50%, -50%);
  background-image: url(../images/svg/arrow-right.svg);
  background-repeat: no-repeat;
  background-size: 0.7027818448rem 0.7027818448rem;
  width: 0.7027818448rem;
  height: 0.7027818448rem;
}
@media (hover: hover) and (pointer: fine) {
  .breadcrumbs ul li:not(:last-of-type) a:hover, .breadcrumbs ul li:first-of-type a:hover {
    color: #E9B566;
  }
}
.breadcrumbs ul li:last-of-type a {
  opacity: 0.3;
  cursor: default;
  pointer-events: none;
}
.breadcrumbs ul li:first-of-type a {
  opacity: 1;
}
.breadcrumbs ul li + li {
  margin-left: 2.8111273792rem;
  width: max-content;
}
.breadcrumbs__black ul li a {
  color: #3C392E;
}
.breadcrumbs__black ul li:not(:last-of-type)::after, .breadcrumbs__black ul li:first-of-type::after {
  background-image: url(../images/svg/arrow-right-orange.svg);
}
.breadcrumbs__black ul li + li {
  margin-left: 2.8111273792rem;
  width: max-content;
}

.text-collapse {
  position: relative;
  overflow: hidden;
  transition: max-height 0.5s, opacity 0.5s;
  cursor: pointer;
}
.text-collapse.show::after {
  opacity: 0;
}
.text-collapse::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5.6222547584rem;
  background: linear-gradient(0deg, #f4f0ec 6%, rgba(255, 255, 255, 0));
  pointer-events: none;
  transition: opacity 0.5s;
  opacity: 1;
}
@media (max-width: 899px) {
  .text-collapse::after {
    display: none;
  }
}

